body {
  font-family: Roboto, sans-serif;
}

button {
  cursor: pointer;
}

.search-form {
  background-color: #404bbf;
  justify-content: center;
  gap: 8px;
  margin-bottom: 20px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.gallery {
  max-width: 1024px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  width: calc(33.3333% - 3.33333px);
  margin-bottom: 20px;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card .info {
  height: 48px;
  background: #f0f0f0;
  gap: 8px;
  padding: 10px;
  display: flex;
}

.info-key {
  font-size: 16px;
  font-weight: 700;
}

.info-value {
  font-size: 12px;
  font-weight: 400;
}

.info-item {
  flex-wrap: wrap;
  justify-content: center;
  margin: 5px auto;
  display: flex;
}

.load-more {
  color: #e7e9fc;
  background: #404bbf;
  border-radius: 4px;
  margin: 16px auto;
  padding: 8px 16px;
  display: none;
}

.load-more:hover {
  background: #333ebd;
}

